import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import {FormControl, Validators, FormGroup} from '@angular/forms';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute} from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/[0-9]{6}/;
const PHONE_REGEX=/^[91]{2}-[80]{2}-[0-9]{6}$/;
const AMOUNT=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./contract.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ FLY_IN_OUT_ANIMATION, ROUND_ANTICLOCK_ANIMATION],
})
export class ContractComponent implements OnInit,AfterViewInit {
	@ViewChild(SmartSearchComponent)smart: SmartSearchComponent;
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ListComponent) list: ListComponent;
	 
	listHide: boolean = true; 
	animateList: string = 'in';
	smartHide: boolean = true;
	animateSmart: string = 'out';
	contractId = 0;  
	contractForm: FormGroup;  
	contract = new ContractData();
	public msg = []; 
 	branches = []; 
 	bootstrapFromValues = []; 
 	contractSummaryData = []; 
 	reporttypes = [
		{key:'1', name:'Contract Details'}
		
	]; 
	 exportformats = [
		{key:'1', name:'MS Excel'},
		 {key:'12', name:'HTML'}
	]; 
 	grandTotal:string; 
	
     constructor(private http: HttpClient, private route:ActivatedRoute ,private router: Router) {
		 this.populateDropDownData();
		 this.getTodaysDate();
    }

	 
	  ngOnInit() {
		  
		this.contractForm = new FormGroup({	
			'branchsIds' : new FormControl(''),
			'reporttype' : new FormControl(''),
			'exportfromat' : new FormControl(''),
		});
}
	    get branchsIds(){ return this.contractForm.get('branchsIds')};
	    get reporttype(){ return this.contractForm.get('reporttype')};
	    get exportfromat(){ return this.contractForm.get('exportfromat')};
	 
	  
	  toggleAnimation(){
		this.router.navigate(['service/contracts-list/contractAdd'], {queryParams : { contractId : this.contractId}});
	  }
	  
	 
	   populateDropDownData(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		  this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/branch',{},
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.branches = data['populateBranch'];
			  															});
		
	  }
	
	
     ngAfterViewInit(){
	
	  $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	 }
	 
	 getTodaysDate(){
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/currentdate',
							 			JSON.stringify({ requestType : 'shortDate' }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.contract.startDate= data['date'];
				  														this.contract.endDate= data['date'];
			  															});
	 }
	 
	  onSubmit(){
		   this.listHide = false;
		  this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.contract[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
		this.contract['requestType']='landing';
		    const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/service/contract',
								 JSON.stringify(this.contract),
								{ headers, withCredentials: true }).subscribe(data =>{
			   					this.contractSummaryData = data['populateData'];
			   console.log(this.contractSummaryData)
			   					this.grandTotal = data['grandTotal'];
																	}); 
		 console.log(this.contract);
		   
	  }
	 }

	
export class ContractData
{
	branchsIds: string;
	reporttype: string;
	exportfromat: string;
	startDate: string;
	endDate: string;
}	
	


